import React from 'react';
import { Container, Typography } from '@mui/material';
import StockOutSection from '../components/StockOutSection';

function StockOutPage() {
  return (
    <Container>
      <Typography variant="h4" gutterBottom align="center" color="primary" sx={{ fontWeight: 'bold', marginTop: 5 }}>
        Stock Out Information
      </Typography>
      <StockOutSection />
    </Container>
  );
}

export default StockOutPage;
