import React, { useEffect, useState } from "react";
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper } from "@mui/material";
import { db } from "../firebase"; // Import Firebase Firestore (make sure to configure it)
import { collection, getDocs } from "firebase/firestore"; // Firestore methods

const StockOutSection = () => {
  const [stockOutData, setStockOutData] = useState([]);

  // Fetch stock-out data from Firestore
  useEffect(() => {
    const fetchStockOutData = async () => {
      try {
        const querySnapshot = await getDocs(collection(db, "stockOut"));
        const stockOutItems = querySnapshot.docs.map((doc) => doc.data());
        setStockOutData(stockOutItems);
      } catch (error) {
        console.error("Error fetching stock-out data: ", error);
      }
    };

    fetchStockOutData();
  }, []);

  // Function to format the date as "Day Month Year" without time
  const formatDate = (timestamp) => {
    if (timestamp && timestamp.seconds) {
      const date = new Date(timestamp.seconds * 1000); // Convert seconds to Date
      return date.toLocaleDateString("en-GB", { // UK format - Day Month Year
        year: "numeric",
        month: "long",
        day: "numeric",
      });
    }
    return "N/A"; // Fallback if timestamp is missing or invalid
  };

  return (
    <div>
      {stockOutData && stockOutData.length > 0 ? (
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Model</TableCell>
                <TableCell>Quantity</TableCell>
                <TableCell>To</TableCell>
                <TableCell>Date</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {stockOutData.map((stockOutItem, index) => {
                const timestamp = stockOutItem.timestamp || {}; // Fallback to empty object if no timestamp
                const date = formatDate(timestamp); // Format the date without time

                return (
                  <TableRow key={index}>
                    <TableCell>{stockOutItem.model}</TableCell>
                    <TableCell>{stockOutItem.quantity}</TableCell>
                    <TableCell>{stockOutItem.to}</TableCell>
                    <TableCell>{date}</TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      ) : (
        <p>No stock-out data available.</p>
      )}
    </div>
  );
};

export default StockOutSection;
